import React from 'react';
const PersonFill = (props) => {
    let left = props.left ? props.left : 0;
    let top = props.top ? props.top : 0;
    let size = props.size ? props.size : 20;
    let color = props.color ? props.color : 'currentColor';
    return (
        <svg xmlns="http://www.w3.org/2000/svg" width={size} height={size} fill={color} style={{transform:"translate("+left+"px, "+top+"px)"}} className="bi bi-person-fill" viewBox="0 0 16 16" id={"personFill"}>
          <path d="M3 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H3zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6z"/>
        </svg>
    )
};

export default PersonFill;
